import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MainAboutComponent } from './main-about/main-about.component';
import { PolicyAboutComponent } from './policy-about/policy-about.component';
import { TermsAboutComponent } from './terms-about/terms-about.component';
// import { StoreAboutComponent } from './store-about/store-about.component';
// import { VoucherListAboutComponent } from './store-about/voucher-list-about/voucher-list-about.component';
// import { VoucherItemAboutComponent } from './store-about/voucher-item-about/voucher-item-about.component';

const AboutRoutes: Routes = [
  { path: '', component: MainAboutComponent, children: [
      {path: '', redirectTo: 'policy', pathMatch: 'full'},
      {
        path: 'policy', component: PolicyAboutComponent
      },
      {
        path: 'terms', component: TermsAboutComponent
      }
    ]
  }

];

@NgModule({
  imports: [RouterModule.forChild(AboutRoutes)],
  exports: [RouterModule]
})
export class AboutRoutingModule { }



