import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AgmCoreModule } from '@agm/core';
import { NglModule } from 'ng-lightning/ng-lightning';

import { AboutRoutingModule } from './about.routing';
import { MainAboutComponent } from './main-about/main-about.component';
import { PolicyAboutComponent } from './policy-about/policy-about.component';
import { TermsAboutComponent } from './terms-about/terms-about.component';

@NgModule({
  imports: [
    CommonModule,
    AboutRoutingModule,
    AgmCoreModule,
    NglModule
  ],
  declarations: [MainAboutComponent,
    PolicyAboutComponent,
    TermsAboutComponent]
})
export class AboutModule { }



